body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

input {
  font-size: 16px;
}
.timer__lap {
  display: -webkit-flex;
  display: flex;
  margin: 0 auto 4px;
}

.lap__item {
  padding: 4px 8px;
}

.lap__name {
  border: none;
}
.btn {
  padding: 4px 16px;
  font-size: 16px;
}

.btn span {
  display: -webkit-flex;
  display: flex;
}

.btn.disabled svg {
  opacity: .5;
}
body {
  font-size: 16px;
}

td {
  padding: 2px 6px;
}

tbody tr:last-child td {
  border-bottom: 1px solid #ccc;
}

.timer__container {
  max-width: 500px;
  margin: 32px auto;
}

.timer__time {
  margin-bottom: 8px;
  font-size: 32px;
  font-weight: bolder;
  text-align: center;
}

.timer__wrap {
  text-align: center;
}

.timer__current-lap {
  display: inline-block;
  margin-bottom: 16px;
}

.timer__current-lap-time {
  padding-right: 10px;
  text-align: right;
}

.timer__current-lap-name {
  padding-left: 10px;
  border: none;
}

.timer__controls {
  margin-bottom: 16px;
  text-align: center;
}

.icon__start svg {
  width: 16px;
  height: 16px;
}

.icon__pause svg {
  width: 16px;
  height: 16px;
}

.icon__reset svg {
  width: 16px;
  height: 16px;
}

.timer__controls button:first-child {
  margin-right: 10px;
}

.timer__laps {
  margin: 0 auto;
  text-align: left;
}

.timer__total {
  font-weight: bold;
}
